___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
}

body {
    margin: 0;
    display: grid;
    grid-template-rows: 100px 1fr 50px;
    height: 100vh;
}

header {
    display: flex;
    justify-content: center;
}

nav {
    display: flex;
    gap: 32px;
    align-items: center;
}

button {
    border: none;
    background-color: black;
    color: white;
    font-size: 15px;
    padding: 0.5em 1.5em;
    cursor: pointer;
}

img {
    width: 100%;
    height: auto;
}

#content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.cards-div {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.card {
    display: flex;
    width: 500px;
    justify-content: space-between;
}

.card p {
    margin: 10px;
}

.card:not(:first-child) {
    border-top: 1px solid black;
}

.about-div {
    max-width: 600px;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;IACT,aAAa;IACb,kCAAkC;IAClC,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,eAAe;IACf,oBAAoB;IACpB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,8BAA8B;AAClC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,gBAAgB;AACpB","sourcesContent":["* {\n    box-sizing: border-box;\n}\n\nbody {\n    margin: 0;\n    display: grid;\n    grid-template-rows: 100px 1fr 50px;\n    height: 100vh;\n}\n\nheader {\n    display: flex;\n    justify-content: center;\n}\n\nnav {\n    display: flex;\n    gap: 32px;\n    align-items: center;\n}\n\nbutton {\n    border: none;\n    background-color: black;\n    color: white;\n    font-size: 15px;\n    padding: 0.5em 1.5em;\n    cursor: pointer;\n}\n\nimg {\n    width: 100%;\n    height: auto;\n}\n\n#content {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\nfooter {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.cards-div {\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n}\n\n.card {\n    display: flex;\n    width: 500px;\n    justify-content: space-between;\n}\n\n.card p {\n    margin: 10px;\n}\n\n.card:not(:first-child) {\n    border-top: 1px solid black;\n}\n\n.about-div {\n    max-width: 600px;\n}"],"sourceRoot":""}]);
